:root {
  --font-family: "Geologica", sans-serif;
  --second-family: "NEXT ART", sans-serif;
  --third-family: "Grafita", sans-serif;
  --font3: "Corsa Grotesk", sans-serif;
  --font4: "Rubik", sans-serif;
  --content-width: 176rem;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --h: #3c3c3c;
  --button: #323232;
  --line: #bebebe;
  --text: #787878;
  --color-input: #aaa;
  --hover-button: #dcdfe3;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable value-keyword-case */
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

html {
  font-size: 0.5208333333vw;
}
@media (min-width: 1920px) {
  html {
    font-size: 10px;
  }
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
  background: #eceff3;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
  padding: 1.2rem 3.8rem;
  border: 1px solid var(--line);
  border-radius: 0.4rem;
  font-weight: 300;
  font-size: 1.4rem;
  line-height: 100%;
  text-align: center;
  color: var(--button);
  transition: 0.2s ease-in-out;
}
@media (any-hover: hover) {
  .btn:hover {
    background: var(--hover-button);
  }
}
.btn svg {
  width: 1.2rem;
  height: 1.2rem;
  fill: #c4c6cb;
}

.title1 {
  margin: 0;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 100%;
  text-transform: uppercase;
  color: var(--h);
}

.title2 {
  margin: 0;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 4rem;
  line-height: 100%;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: var(--h);
}
.title2--white {
  color: #fff;
  letter-spacing: -0.04em;
}

.desc {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 300;
  font-size: 1.8rem;
  line-height: 100%;
  letter-spacing: -0.02em;
  color: var(--text);
}
.desc p {
  margin: 0;
}
.desc p:not(:last-child) {
  margin-bottom: 2rem;
}

.header {
  padding: 2rem 0;
  border-bottom: 1px solid var(--button);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.header__logo {
  display: block;
  width: 22rem;
  height: 1.8rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.header__logo svg {
  width: 100%;
  height: 100%;
  fill: var(--button);
}
.header__soc {
  display: flex;
  align-items: center;
  gap: 2.4rem;
}
.header__right {
  display: flex;
  align-items: center;
  gap: 4rem;
}
.header__link {
  display: block;
  width: 2.4rem;
  height: 2.4rem;
}
.header__link svg {
  width: 100%;
  height: 100%;
  fill: var(--button);
}

.hero {
  width: 100%;
  position: relative;
  padding: 6.4rem 0 20rem;
}
.hero canvas {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.hero__title {
  text-align: center;
  margin-bottom: 6.4rem;
}
.hero__items {
  display: flex;
  justify-content: space-between;
}

.hero-item {
  display: flex;
  gap: 2.8rem;
  align-items: center;
}
.hero-item__obj {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 14rem;
  line-height: 100%;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  text-align: right;
  color: var(--h);
}
.hero-item__text {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 3.2rem;
  line-height: 140%;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #9b9b9b;
}

.offer {
  margin-bottom: 8rem;
}
.offer__title {
  margin-bottom: 4rem;
}
.offer__img {
  position: relative;
  width: 100%;
  height: auto;
  border-bottom: 1px solid var(--line);
  border-top: 1px solid var(--line);
}
.offer__img::after {
  content: "";
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  width: 100%;
  height: 20rem;
  background: linear-gradient(180deg, #eceff3 0%, rgba(242, 245, 251, 0) 100%);
}
.offer__img::before {
  content: "";
  position: absolute;
  z-index: 2;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 10rem;
  background: linear-gradient(360deg, #eceff3 0%, rgba(242, 245, 251, 0) 100%);
}
.offer__img img {
  width: 100%;
  height: auto;
}

.main-portfolio {
  padding: 8rem 0;
}
.main-portfolio__header {
  padding-bottom: 4rem;
  margin-bottom: 6.4rem;
  border-bottom: 1px solid var(--line);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.main-portfolio__header--center {
  justify-content: center;
}
.main-portfolio__btn--plug {
  opacity: 0;
  pointer-events: none;
  cursor: default;
  user-select: none;
}
.main-portfolio__items {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 7rem;
  margin-bottom: 6.4rem;
}
.main-portfolio__bottom {
  display: flex;
  justify-content: center;
}

.other .main-portfolio__header {
  border: none;
  padding-bottom: 0;
  margin-bottom: 6.4rem;
}

.portfolio-item {
  height: 84.5rem;
  position: relative;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}
@media (any-hover: hover) {
  .portfolio-item:hover .portfolio-item__img:nth-child(2) {
    z-index: 3;
    opacity: 1;
  }
}
.portfolio-item__img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.portfolio-item__img img {
  width: 100%;
  height: 100%;
}
.portfolio-item__img:nth-child(2) {
  z-index: -1;
  opacity: 0;
  transition: 0.12s ease-in-out;
}
.portfolio-item__info {
  position: relative;
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.portfolio-item__name {
  margin-bottom: 1.2rem;
  text-align: right;
}
.portfolio-item__tags {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}
.portfolio-item__tag {
  border-radius: 5rem;
  padding: 1.2rem 2rem;
  background: rgba(0, 0, 0, 0.1);
  text-align: center;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 100%;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  color: #fcffff;
}

.disc {
  padding-top: 25rem;
  padding-bottom: 20rem;
}
.disc__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 6.4rem;
  font-weight: 300;
  gap: 2rem;
  margin-bottom: 31rem;
}
.disc__title span:first-child {
  margin-left: 20rem;
}
.disc__title span:last-child {
  margin-left: -32rem;
}
.disc__bottom {
  display: flex;
  justify-content: center;
}

.footer {
  background: var(--h);
  position: relative;
  overflow: hidden;
  padding-bottom: 2rem;
}
.footer__logo {
  width: 100%;
  height: auto;
  transform: translateY(-1.5rem);
  margin-bottom: 7rem;
}
.footer__logo img {
  width: 100%;
  height: auto;
}
.footer__nav {
  display: flex;
  justify-content: space-around;
  margin-bottom: 6.4rem;
}
.footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__copy {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 100%;
  letter-spacing: -0.04em;
  color: #9b9b9b;
}
.footer__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 3.2rem;
}
.footer__label {
  margin-bottom: 1.2rem;
  display: block;
  font-weight: 250;
  font-size: 1.6rem;
  line-height: 100%;
  letter-spacing: -0.04em;
  color: #c3c3c3;
}
.footer__link {
  font-weight: 300;
  font-size: 1.6rem;
  line-height: 100%;
  letter-spacing: -0.04em;
  color: #fff;
}
.footer__link--big {
  font-size: 1.8rem;
}
.footer__soc {
  display: flex;
  align-items: center;
  gap: 4rem;
}
.footer__soc li {
  flex-shrink: 0;
  width: 2.8rem;
  height: 2.8rem;
}
.footer__soc a {
  display: block;
  width: 100%;
  height: 100%;
}
.footer__soc a svg {
  width: 100%;
  height: 100%;
  fill: #fff;
}

.form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 6.4rem 3.2rem;
}
.form__label {
  position: relative;
  display: block;
  width: 100%;
}
.form__input {
  width: 100%;
  height: 6.4rem;
  background: none;
  border: none;
  border-bottom: 1px solid var(--button);
  font-family: var(--font-family);
  font-weight: 300;
  font-size: 2.4rem;
  line-height: 100%;
  letter-spacing: -0.04em;
  color: var(--button);
  padding: 1rem;
}
.form__input::placeholder {
  color: var(--color-input);
  transition: 0.15s ease-in-out;
}
.form__input:focus {
  outline: none;
  border-color: #000;
}
.form__input:focus::placeholder {
  color: var(--button);
}
.form__attach {
  grid-column: 2 span;
  position: relative;
}
.form__file {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid var(--line);
  border-radius: 0.4rem;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
  font-weight: 300;
  font-size: 1.2rem;
  line-height: 140%;
  color: var(--color-input);
}
.form__file svg {
  width: 1rem;
  height: 2rem;
}
.form__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-column: 2 span;
}
.form__btn {
  width: 100%;
  max-width: 22rem;
}
.form__box {
  display: block;
  width: 1.6rem;
  height: 1.6rem;
  flex-shrink: 0;
  position: relative;
  border: 1px solid var(--button);
}
.form__box::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background-color: var(--button);
  background-image: url("./../img/check.svg");
  background-size: 1.4rem 1rem;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0;
}
.form__agree {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 1.2rem;
  font-family: var(--font-family);
  font-weight: 300;
  font-size: 1.6rem;
  line-height: 140%;
  color: var(--text);
}
.form__agree input:checked ~ .form__box::before {
  opacity: 1;
}

.cta {
  padding: 8rem 0 14rem;
}
.cta__container {
  max-width: 116.6rem;
  margin: 0 auto;
}
.cta__header {
  display: flex;
  align-items: flex-start;
  margin-bottom: 12rem;
}
.cta__desc {
  margin: 0;
  font-weight: 300;
  font-size: 1.8rem;
  line-height: 100%;
  letter-spacing: -0.02em;
  text-align: right;
  color: var(--text);
}
.cta__title {
  flex-shrink: 0;
  margin: 0;
  font-weight: 300;
  font-size: 6.4rem;
  line-height: 100%;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: var(--h);
}
.cta__title span {
  display: block;
}
.cta__title span:first-child {
  margin-left: -23rem;
}

.temp__container {
  height: 90rem;
}
.temp__container img {
  width: 100%;
  height: 100%;
}

.temp-info {
  padding: 8rem 0;
}
.temp-info__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 2rem;
}
.temp-info__side {
  grid-column: 4 span;
}
.temp-info__date {
  font-family: var(--second-family);
  margin-bottom: 7.2rem;
}
.temp-info__item {
  font-family: var(--font-family);
  font-weight: 250;
  font-size: 1.4rem;
  line-height: 100%;
  color: var(--button);
}
.temp-info__item:not(:last-child) {
  margin-bottom: 2rem;
}
.temp-info__content {
  grid-column: 5 span;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: 2rem;
}
.temp-info__mob {
  grid-column: 3 span;
  height: 75rem;
}
.temp-info__title {
  margin-bottom: 7.2rem;
}
.temp-info__link {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 100%;
  letter-spacing: -0.04em;
  color: var(--button);
}

.temp-mob {
  position: relative;
  display: inline-block;
}
.temp-mob__mobile {
  position: relative;
  z-index: 1;
  overflow: hidden;
  height: 100%;
}
.temp-mob__mobile img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.temp-mob__slider {
  height: 100%;
}
.temp-mob .swiper-slide {
  height: auto;
}
.temp-mob__image {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 89%;
  background-color: #000;
  mask-image: url("./../img/mob-template.webp");
  mask-size: contain;
  mask-repeat: no-repeat;
  mask-position: center;
}
.temp-mob__image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.temp-pc {
  position: relative;
  display: inline-block;
  overflow: hidden;
}
.temp-pc__pc {
  position: relative;
  z-index: 1;
  height: 100%;
  overflow: hidden;
}
.temp-pc__pc img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.temp-pc .swiper-slide {
  width: 100%;
  height: 100%;
}
.temp-pc__image {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 5rem;
  overflow: hidden;
  padding: 2rem;
  padding-top: 2.5rem;
}
.temp-pc__image img {
  height: 100%;
  width: 100%;
  object-position: top;
  object-fit: cover;
}

.temp-slider {
  padding: 8rem 0;
}
.temp-slider__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 2rem;
  align-items: center;
}
.temp-slider__mob {
  grid-column: 4 span;
}
.temp-slider__right {
  grid-column: 8 span;
}
.temp-slider__pc {
  margin-bottom: 2.5rem;
}
.temp-slider__control {
  display: flex;
  align-content: center;
  gap: 4.3rem;
  padding-left: 24rem;
}
.temp-slider__arr {
  width: 2.2rem;
  height: 1.8rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.temp-slider__arr svg {
  width: 100%;
  height: 100%;
}